"use client";

import { forwardRef, useRef, useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import clsx from "clsx";

import HomeEventCard from "@/components/snippets/homeeventcar/HomeEventCard";
import "./homeevent.css";

gsap.registerPlugin(ScrollTrigger);

// Remove any code that could cause hydration mismatch (e.g., Math.random(), Date.now(), etc.)
// The AnimatedTitle below is a direct copy of the one in src/components/utils/animatedtitle/AnimatedTitle.jsx
// to ensure no SSR/client mismatch

const AnimatedTitle = ({ title, containerClass }) => {
  const containerRef = useRef(null);

  useEffect(() => {
    const ctx = gsap.context(() => {
      const titleAnimation = gsap.timeline({
        scrollTrigger: {
          trigger: containerRef.current,
          start: "100 bottom",
          end: "center bottom",
          toggleActions: "play none none reverse",
        },
      });

      titleAnimation.to(
        ".animated-word",
        {
          opacity: 1,
          transform: "translate3d(0, 0, 0) rotateY(0deg) rotateX(0deg)",
          ease: "power2.inOut",
          stagger: 0.02,
        },
        0
      );
    }, containerRef);

    return () => ctx.revert();
  }, []);

  return (
    <div ref={containerRef} className={clsx("animated-title", containerClass)}>
      {title.split("<br />").map((line, index) => (
        <div
          key={index}
          className="flex-center flex-wrap gap-2 px-10 max-w-full md:gap-3"
        >
          {line.split(" ").map((word, idx) => (
            <span
              key={idx}
              className="animated-word"
              dangerouslySetInnerHTML={{ __html: word }}
            />
          ))}
        </div>
      ))}
    </div>
  );
};

const HomeEvent = forwardRef((props, ref) => {
  const homeEventContainer = useRef(null);

  // Remove any non-deterministic code for SSR
  // Use a fixed array for cards to avoid hydration mismatch
  const cards = [
    {
      id: "card-1",
      frontSrc: "/img/amity_logo.png",
      frontAlt: "Card Image",
      backText: "Your card Details Appear here",
    },
    {
      id: "card-2",
      frontSrc: "/img/amity_logo.png",
      frontAlt: "Card Image",
      backText: "Your card Details Appear here",
    },
    {
      id: "card-3",
      frontSrc: "/img/amity_logo.png",
      frontAlt: "Card Image",
      backText: "Your card Details Appear here",
    },
    {
      id: "card-4",
      frontSrc: "/img/amity_logo.png",
      frontAlt: "Card Image",
      backText: "Your card Details Appear here",
    },
  ];

  // Ensure ref is always an array if provided
  const handleCardRef = (el, index) => {
    if (ref && typeof ref === "object" && ref.current) {
      if (!Array.isArray(ref.current)) {
        ref.current = [];
      }
      ref.current[index] = el;
    }
  };

  return (
    <section className="HomeEventSec" ref={homeEventContainer}>
      <AnimatedTitle
        title="Events Overview"
        containerClass="mt-5 !text-black text-center"
      />
      {cards.map((card, index) => (
        <HomeEventCard
          key={card.id}
          id={card.id}
          frontSrc={card.frontSrc}
          frontAlt={card.frontAlt}
          backText={card.backText}
          ref={(el) => handleCardRef(el, index)}
        />
      ))}
    </section>
  );
});

export default HomeEvent;
